<template>
    <div>
        <v-layout class="pa-4" column>

            <v-flex class="pa-4" xs12 md10 offset-md1 lg8 offset-lg2 xl6 offset-xl3>
                <h4 class="bolder">{{items.excesses.title}}</h4>
                <!--<v-divider class="mt-4 ml-2"></v-divider>-->
            </v-flex>
            <v-flex class="pa-4" v-for="excess in items.excesses.data" xs12 md10 offset-md1 lg8 offset-lg2 xl6
                    offset-xl3>
                <find-to-match :data="excess"></find-to-match>
            </v-flex>
            <v-flex class="pa-4" xs12 md10 offset-md1 lg8 offset-lg2 xl6 offset-xl3>
                <h4 class="bolder">{{items.title}}</h4>
                <!--<v-divider class="mt-4 ml-2"></v-divider>-->
            </v-flex>
            <v-flex class="pa-4" xs12 md10 offset-md1 lg8 offset-lg2 xl6 offset-xl3>
                <v-btn class="btnText" v-for="item in pairs" large @click.native="clickPair(item)"
                       :success="correct && pair.includes(item)"
                       :error="wrong && pair.includes(item)"
                       :disabled="item.disabled"
                       :primary="pair.includes(item)">
                    {{item.data}}
                </v-btn>
            </v-flex>
            <v-flex xs12 md10 offset-md1 lg8 offset-lg2 xl6 offset-xl3>
                <v-layout row wrap justify-center>
                    <v-card flat v-for="(letter,index) in items.winWord.split('')"
                            class="ml-2 mr-2"
                    >
                        <transition name="bounce">
                            <v-card v-if="index<progress"
                                    class="pl-2 pr-2 bolder accent white--text"
                            >
                                <v-card-text class="headline">{{letter}}</v-card-text>
                            </v-card>
                        </transition>
                        <!--<transition name="fade">-->
                        <v-card class="pl-2 pr-2 btn--disabled transparent--text" v-if="index>=progress">
                            <v-card-text class="headline">{{letter}}</v-card-text>
                        </v-card>
                        <!--</transition>-->
                    </v-card>
                </v-layout>
            </v-flex>
        </v-layout>
    </div>
</template>

<script>
    import FindToMatch from './FindToMatch.vue'

    export default {
        props: ['items'],
        components: {
            FindToMatch,
        },
        data() {
            return {
                attempt: 0,
                correct: false,
                wrong: false,
                pair: [],
                pairs: [],
                progress: 0,
                bonus: 3,
                correctExcesses: []
            }
        },
        created() {
            this.attempt = 0;
            this.progress = 0;
            this.correct = false;
            this.wrong = false;

            this.$on('setResult', (resultKey) => {
                console.log("setResult Pair: ", resultKey);
                this.correctExcesses.push(resultKey);
                this.gameOver();
            });
            this.items.pairs.forEach((item, index) => {
                this.pairs.push({key: index, data: item[0]});
                this.pairs.push({key: index, data: item[1]});
            });
            this.$parent.shuffle(this.pairs);
            this.progress = 0;
        },
        beforeDestroy() {
            this.pair = [];
            this.pairs.splice(0, this.pairs.length);
        },
        methods: {
            getResultPair() {
                if (this.progress == this.pairs.length / 2) {
                    let result = 2 * (this.progress + this.bonus) / this.attempt * 100;
                    result = result > 100 ? 100 : result;
                    result = result < 0 ? 0 : result;
                    console.log("getResultPair: ", result);
                    return result;
                }
                return -1;
            },
            getResultMatch() {
                let max = this.items.excesses.data.length;
                if (this.correctExcesses.length == max) {
                    let countCorrect = this.correctExcesses.filter((item) => {
                        return item == true
                    }).length;
                    let result = countCorrect / max * 100;
                    result = result > 100 ? 100 : result;
                    result = result < 0 ? 0 : result;
                    console.log("getResultMatch: ", result);
                    return result;
                }
                return -1;
            },
            gameOver() {
                if (this.getResultPair() >= 0 && this.getResultMatch() >= 0) {
                    let result = this.getResultPair() * 0.7 + this.getResultMatch() * 0.3;
                    console.log("WIN", result);

                    this.$router.push({
                        name: 'res',
                        params: {result: result, resId: this.$route.params.gameId}
                    });

                }
            },
            clickPair(item) {
                let pair = this.pair;
                this.attempt++;
                if (pair.length >= 2) return;

                if (pair.includes(item)) {
                    pair.splice(pair.indexOf(item), 1);
                    this.attempt -= 2;
                } else {
                    pair.push(item);
                }

                if (pair.length == 2) {

                    if (pair[0].key == pair[1].key) {
                        this.correct = true;
                        this.progress++;
                    } else {
                        this.wrong = true;
                    }

                    setTimeout(() => {
                        if (this.correct) {
                            let index1 = this.pairs.indexOf(pair[0]);
                            this.pairs[index1].disabled = true;
                            let index2 = this.pairs.indexOf(pair[1]);
                            this.pairs[index2].disabled = true;
                        }

                        this.correct = false;
                        this.wrong = false;
                        pair.splice(0, 2);

                        console.log("this.progress: ", this.progress);
                        console.log("this.pairs: ", this.pairs.length / 2);
                        console.log("attempt: ", this.attempt);
                        this.gameOver();
                    }, 500);


                }
            }
        }
    }

</script>


